import { Component, Input, OnInit, SimpleChanges } from '@angular/core';

@Component({
  selector: 'ls-loading',
  templateUrl: './loading.component.html',
  styleUrls: ['./loading.component.scss']
})
export class LoadingComponent implements OnInit {

  // _size | Default: md
  @Input() sm;
  @Input() md;
  @Input() lg;

  // _colorScheme | Default: dark
  @Input() light;

  // _parent | Default: page
  @Input() button;
  @Input() page;

  // _loading
  @Input() loading;

  // _content | Default: 'bootstrap'
  @Input() content;

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.loading) {
      this._loading = (typeof this.loading === 'boolean') ? 
        this.loading : 
        (changes.loading.currentValue >= 1) ? false : true;
    }
  }

  _size: string;
  _colorScheme: string;
  _parent: string;
  _loading: boolean;
  _content: string;

  ngOnInit(): void {

    // Parent
    if (this.button !== undefined) this._parent = 'button'
    else if (this.page !== undefined) this._parent = 'page'
    else this._parent = 'page'

    // Size
    if (this.sm !== undefined) this._size = 'small'
    else if (this.md !== undefined) this._size = 'medium'
    else if (this.lg !== undefined) this._size = 'large'
    else this._size = (this._parent === 'page') ? 'medium' : 'small'

    // Color
    if (this.light === undefined) this._colorScheme = 'dark'
    else this._colorScheme = 'light'

    // Content
    this._content = (this.content === undefined) ? 'bootstrap' : this.content;
  }

}
